    openDialogOTP(numbr) {
      console.log("num",numbr);
      const dialogRef = this.dialog.open(OtpVerifyComponent, {
        data: { number: numbr, type: "numbr" },
        disableClose: true,
      });
      dialogRef.afterClosed().subscribe(submit => {
        if(submit){
          this.getOTPforSave( this.cashdepositValues,this.cashdepositType,'');
        }
       });
    }
   
