  fetchByAccountNum(accNum) {
    console.log('Account Numebr', accNum);
    this.flag1 = true;
    this.displayFlag=true;
    this.ifQRvariable = true;
    if (this.custValidation == 'New Customer' || this.custNameFromLogin !=null) {
      this.hideAccNum = false;
    } else {
      this.hideAccNum = true;
    }
    this.hideDetails = true;
    this.apiService.fetchByaccNum(accNum).subscribe((res: any) => {
      console.log('statement',res);
      this.accountForm.controls.accountCurrency.setValue(res?.currency);
      if(accNum != res?.accountId || res.accountId == null ){
        
        this.snack.open("Account Number Doesn't Exist" , "OK", {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right',
          panelClass: ['snackbar'],
        });
        this.accountForm.reset();
        this.userName='';
        this.accountBal ='';
        this.curr ='';
      }
      else{
      console.log('Account Number Rsp', res);
      this.dataService.sendaccountFormData(res);
      this.userName = res.accHolderName;
      this.accountBal = res.accBalance;
      this.custmerID = res.customerId;
      this.accountNumber = res.accountId;
      this.accountForm.value.accountBalance = res.accBalance;
      console.log(this.accountForm);
      this.accountForm.controls.accountBranch.setValue(res.accountBranch);
      this.accountForm.controls.customerId.setValue(res.customerId);

      this.curr = getCurrencySymbol(res.currency, 'narrow');
      this.accountForm.controls.accountType.setValue(res.accountType);
      // this.accountForm.controls.accountBranch.setValue(res.accountBranch);
      this.accountForm.controls.holderName.setValue(this.userName);

      this.getCountrynameValues();
    
      // this.currencyImage = 'currency-flag-'+res.currency.toLowerCase();
      this.currencyImage = 'currency-flag-' + res.currency.toLowerCase();

      this.accountCurrency1 = res.currency;
      console.log(this.ifQRvariable);
      this.custAmountBal(accNum);
      }
      this.apiService
        .fetchByaccDeatilsoncustId(res.customerId)
        .subscribe((resp: any) => {
          this.phonenum = resp[0].phoneNumber;
          localStorage.setItem('otpPhoneNum', this.phonenum);
        });
        (error : HttpErrorResponse) => {
          console.log(error);
          this.errorStatus = error.status;
          if (this.errorStatus == 204) {
            this.snack.open("Account Number Doesn't Exist" , "OK", {
              duration: 4000,
              verticalPosition: 'top',
              horizontalPosition: 'right',
              panelClass: ['snackbar'],
            });
            // this.router.navigateByUrl('/sessions/otp');
          }  
    }
  }
    );

  
  }
  getAccountType(event: any) {
    console.log("get Account Type");
    this.accountNoList = [];
    this.isedit = false;
    this.accountNum1 = [...this.custAccountData].filter((accounNum, index) => {
      return accounNum.accountType == event;
    });
    // this.accountNoList=this.accountNum1
    // this.accountNoList=this.accountNum1.map((acccount)=>
    // {
    //   return acccount.accountId
    // });
    for (let i = 0; i < this.custAccountData.length; i++) {
      if (
        this.custAccountData[i].status == 'APPROVED' &&
        this.custAccountData[i].accountType == event
      ) {
        this.accountNoList.push(this.custAccountData[i].accountId);
      }
    }
    // this.accountNoList.push(12233)
    console.log(this.accountNoList);

    if (this.accountNoList.length <= 1) {
      // this.cashwithdrawForm.controls.accountNumber.patchValue(this.accountNoList[0]);
      this.flag1 = false;
      this.displayFlag=true;
      this.getData(this.accountNoList[0]);

      this.accountForm.controls.accountNumber.setValue(this.accountNoList[0]);
    } else {
      this.getData(this.accountNoList[0]);
      this.flag1 = true;
      this.displayFlag=true;
    }
  }
  getData(custNum) {
    this.hideDetails = true;
    this.hideAccNum = true;
    let val = this.accountNum1.filter((value) => {
      return value.accountId == custNum;
    });

    this.idList = [...this.custAccountData].filter(
      (item) => item.accountType === val[0].accountType
    );
    // this.accountForm.controls.accountCurrency.setValue(val[0].accountCurrency);
    //  this.accountForm.controls.accountBranch.setValue(this.branch);
    //  this.selected=val[0].accountCurrency;
    this.accountCurrency1 = val[0].accountCurrency;
    this.curr = getCurrencySymbol(val[0].accountCurrency, 'narrow');

    this.currencyImage =
      'currency-flag-' + val[0].accountCurrency.toLowerCase();
    this.accountBal = val[0].amount;
    localStorage.setItem('AccountBal', this.accountBal);
    this.accountForm.controls.accountNumber.setValue(val[0].accountId);

    this.custAmountBal(val[0].accountId);
    // this.accountForm.controls.accountBranch.setValue(val[0].accountBranch);
  }
  custAmountBal(custNum) {
    console.log(custNum);
    var account = this.idList.filter(function (record) {
      if (record.accountId == custNum) {
        return record;
      }
    });
    console.log(account);
    this.accountForm.controls.accountCurrency.setValue(account[0]?.accountCurrency?account[0]?.accountCurrency:this.accountCurrency1);
    if (account.length > 0) {
      this.accNum = account[0].accountId;
      this.accountBal = account[0].amount;
      this.selectedAccountInfo = account[0];
    }
    console.log(this.accountBal);
    this.accountForm.controls.accountBalance.setValue(this.accountBal);
    console.log(this.accountForm);
    localStorage.setItem('AccountBal', this.accountBal);
  }
